html {
    scroll-behavior: smooth;
}
body {
    font-family: 'gotham','NanumSquare', 돋움, sans-serif;
}
.head {
    .mySwiper {
        .swiper-pagination-bullet { 
            width: 16px; 
            height: 16px;
            background-color: rgba(0, 0, 0, 1);
            margin: 2px !important;
        }
        .swiper-pagination-bullet:hover {
            border-radius: 50%; 
            background: #fff; 
            border: 5px solid #000; 
            opacity: 1;
        }
        .swiper-pagination-bullet-active {
            border-radius: 50%; 
            background: #fff; 
            border: 5px solid #000; 
            opacity: 1;
        }
        .swiper-button-next:after {
            content: '>' !important;
            position: absolute;
            line-height: 110px;
            text-align: center;
            font-family: "revicons";
            color: #fff;
            font-size: 20px;
            top: 0px;
            left: 6px;
            z-index: 0;
            width: 30px;
            height: 110px;
            transition: all 0.2s ease-in-out;
            background: rgba(0,0,0,0.25);
            opacity: 1;
        }
        .swiper-button-prev:after {
            content: '<' !important;
            position: absolute;
            line-height: 110px;
            text-align: center;
            font-family: "revicons";
            color: #fff;
            font-size: 20px;
            top: 0px;
            left: -10px;
            z-index: 0;
            width: 30px;
            height: 110px;
            background: rgba(0,0,0,0.25);
            transition: all 0.2s ease-in-out;
            opacity: 1;
        }   
    }
}
