/* -----------nav--------------- */
nav {
    .menu {
        position: relative;
        z-index: 1;
        display: flex;
        flex-flow: row;
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 1px;
        transform: translateX(-15px);
        li {
            a {
                span {
                    font-weight: 500;
                    position: absolute;
                    right: 6px;
                    font-size: 12px;
                }
            }
            ul.menu_1 {
                display: none;
            }
            &:hover {
                ul.menu_1 {
                    display: block;
                }
                ul.last_menu {
                    position: absolute;
                    right: 0;
                    width: 165%;
                    text-align: left;
                    display: flex;
                    flex-direction: row;
                    background-color: #fff;
                    font-weight: 600;
                    transition: .4s;
                }
            }
        }
    }
}
nav .menu>li { text-align: center; }
nav .menu>li:nth-child(1) { width: 112px; }
nav .menu>li:nth-child(2) { width: 107px; }
nav .menu>li:nth-child(3) { width: 107px; }
nav .menu>li:nth-child(4) { width: 103px; }
nav .menu>li:nth-child(5) { width: 116px; }
nav .menu>li:nth-child(6) { 
    width: 135px; 
}
.last_menu>li {
    width: 100%;
}
.menu_2_2 {
    display: flex;
    flex-flow: column;
    padding: 20px;
    width: 100%;
}

.menu_2_2>li>a {
    display: block;
    padding: 10px 12px;
    font-size: 12px;
    width: 100%;
}

.menu_2_2>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .5s;
}

nav .menu>li>a {
    height: 100px;
    line-height: 100px;
    padding: 0 12px;
    color: #fff;
    position: relative;
}
nav .menu>li> ul {
    position: absolute;
    top: 100px;
    background-color: #fff;
    width: 190px;
    text-align: left;
    font-size: 11px;
    font-weight: 400;
}
nav .menu>li> ul>li>a {
    padding: 12px 20px;
    color: #000;
    font-weight: 400;
}
nav .menu>li> ul>li:hover>a {
    background-color: #d6b161;
    color: #fff;
    transition: .5s;
}

nav .menu>li:hover>a {
    color: #d6b161;
    transition: .2s;
} 

 nav .menu>li> ul:nth-child(2)>li {
    position: relative;
    transition: .5s;
}
 nav .menu>li> ul:nth-child(2)>li:first-child strong {
    font-weight: 600;
    font-size: 14px;
    transition: .5s;
    padding-left: 90px;
}
 nav .menu_2_1 {
    position: absolute;
    top: 0; left: 190px;
    width: 190px;
}
 nav .menu2 li:hover .menu_2_1 a {
    background-color: #fff;
    color: #000;
    padding: 13.5px 20px;
    transition: .5s;
}
 nav .menu2 li:hover .menu_2_1 li:hover a {
    background-color:  #d6b161;
    color: #fff;
    transition: .7s;
}
 nav .menu2>li ul {
    display: none;
}
 nav .menu2>li:hover> ul {
    display: block;
    transition: .5s;
}
/* ----------HeaderHead1---------------- */
.header_box1 {
    text-align: center;
    position: absolute; 
    top: 55%; left: 50%; transform: translate(-50%, -50%);
    h2 {
        font-size: 60px;
        font-family: 'Source Sans Pro', sans-serif;
