/* -----------------main------------------- */

.main_about {
    height: 620px;
    padding: 100px 0;
    box-sizing: border-box;
    overflow: hidden;
    font-weight: 500;
}
.main_about_box {
    margin: 0 -15px;
    text-align: center;
}
.main_about_box .txt {
    margin-bottom: 100px;
}
.main_about_box .txt h2 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
    letter-spacing: 2px;
    font-weight: 600;
}
.main_about_box .txt p {
    font-size: 15px;
    color: #7e7e7e;
    padding: 30px 0 0;
    letter-spacing: -0.1px;
    span {
        font-weight: 600;
    }
}
.main_about_box .about_box {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 310px);
    grid-auto-rows: 229px;
    gap: 80px;
    justify-content: center;
}
.main_about_box .box {
    font-size: 15px;
    color: #323232;
    border: 1px solid #e9e9e9;
    position: relative;  
    padding: 25px;
    border-radius: 4px;
    width: 330px;
}
.main_about_box .about_box .img {
    background-color: #222;
    border: 5px solid #fff;
    width: 65px; height: 65px;
    display: block;
    border-radius: 10px;
    position: absolute;
    top: -30px; 
}
.main_about_box .box .img .img_svg {
    display: block;
    width: 30px; height: 30px;
    filter: invert(100%);
    margin: 13px auto;
}
.main_about_box .box:hover .img {
    background-color: #d6b161;
    transition: .7s;
}
.main_about_box .box .about_txt {
    margin: 30px auto;
    text-align: left;
}
.main_about_box .box .about_txt h3 {
    font-size: 18px;
    margin: 40px 0 20px;
    color: #333;
    letter-spacing: 1px;
    font-weight: 600;
}
.main_about_box .box .about_txt p {
    font-size: 15px;
    color: #7e7e7e;
    line-height: 1.5;
    margin-top: 7px;
    letter-spacing: -0.2px;
    font-weight: 500;
    text-transform: none;
    word-spacing: 3px;
    span {
        font-weight: 600;
        letter-spacing: 1px;
    }
}
/* product */
.main_product {
    height: 467px; 
    box-sizing: border-box;
    padding: 100px 0;
    background-color: #f5f5f5;
    font-size: 15px;
    color: #323232;
    margin: 0 auto;
}
.productbox {
    display: grid;
    grid-template-columns: repeat(2,585px);
    justify-content: center;
}
.product_box1, .product_box2 {
    padding: 0 30px;
}
.main_product h3 {
    font-size: 18px;
    margin: 0 0 30px;
    color: #333;
    font-weight: normal;
    line-height: 1.5;
    letter-spacing: 2px;
    font-weight: 600;
}
.product_box1 p {
    font-size: 15px;
    color: #7e7e7e;
    line-height: 30px;
    letter-spacing: -0.2px;
    word-spacing: 1px;
    span {
        font-weight: 600;
    }
}

.main_product .box2_grape {
    color: #323232;
    overflow: visible;
    height: 20px;
    margin-top: 10px;
    margin-bottom: 50px;
    letter-spacing: 1px;
    text-transform: uppercase;
    border-radius: 0;
    background: #eae8e8;
    box-shadow: none;
    font-weight: 600;
    width: 100%;
    position: relative;
    .grape_bg {
        box-sizing: border-box;
        padding-top: 0;
        padding-left: 10px;
        text-align: left;
        color: #fff;
        box-shadow: none;
        font-size: 10px;
        line-height: 20px;
        width: 20%;
        background-color: #222;
        transition: .5s all ease;
    }
    .gr1 {
        animation: bar1 ease 2s;
        width: 60%;
        @keyframes bar1 {
            0% {
                width: 20%;
            }
            100% {
                width: 60%;
            }
        }
    }
    .gr2 {
        animation: bar2 ease 2s;
        width: 50%;
        @keyframes bar2 {
            0% {
                width: 20%;
            }
            100% {
                width: 50%;
            }
        }
    }
    .gr3 {
        animation: bar3 ease 2s;
        width: 95%;
    
        @keyframes bar3 {
            0% {
                width: 20%;
            }
            100% {
                width: 95%;
            }
        }
    }
}
.main_product {
    .pos1 {
        position: absolute;
        top: -28px;
        right: 190px;
    }
    .pos2 {
        position: absolute;
        top: -28px;
        right: 240px;
    }
    .pos3 {
        position: absolute;
        top: -28px;
        right: 6px;
    }
} 
.main_product .grape_peacant {
    display: inline-block;
    min-width: 25px;
    height: 20px;
    padding: 0 8px;
    text-align: center;
    letter-spacing: 0;
    color: #fff;
    border-radius: 30px;
    background-color: rgba(0, 0, 0, .6);
    line-height: 20px;
    font-size: 10px;
    &::before {
        position: absolute;
        bottom: -2px;
        left: 15px;
        display: block;
        width: 5px;
        height: 5px;
        content: "";
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        border-style: none;
        border-color: #666;
        border-right: 1px solid #666;
        -o-border-image: none;
        border-image: none;
        background-color: #666;
    }
}
