                line-height: 30px;
            }
        }
    }
    .mySwiper {
        margin: 20px;
        padding: 90px 0 60px 0;
        img {
            display: block;
            width: 80%;
        }
        .swiper-pagination-bullet {
            width: 12px;
            height: 12px;
            background-color: #eaeaea;
        }
    } 

} 
/* foot */
.main_foot {
    height: 450px;
    background-color: #222;
    padding: 80px 0;
    color: #6f7273;
    h3 {
        font-size: 14px;
        margin-bottom: 30px;
        color: #fff;
        letter-spacing: 1px;
        font-weight: 600;
    }
    .foot_box {
        display: grid;
        grid-template-columns: repeat(4,292.5px);
        grid-auto-rows: 291px;
        justify-content: center;
        .foot_pro {
            padding: 0 30px;
            a {
                margin-bottom: 20px;
            }
            p {
                color: #7e7e7e;
                font-size: 15px;
                margin-bottom: 30px;
                line-height: 30px;
                text-transform: none;
                font-weight: 600;
                letter-spacing: 0.1px;
            }
        }
        .foot_posts {
            padding: 0 30px;
            li {
                border-bottom: 1px solid #333;
                transform: translateY(10px) translateX(-6px);
                a {
                    font-size: 15px;
                    color: #a2a2a2;
                    display: block;
                    line-height: 45px;
                    text-transform: none;
                    font-weight: 600;
                    &::before {
                        margin-right: 10px;
                        padding-right: 15px;
                        content: "";
                        background: url(../img/btn_page_next.png) no-repeat 50% / cover;
                    }
                    &:hover {
                        color: #d6b161;
                        transition: .6s;
                    }
                }
            }
        }
        .foot_link {
            padding: 0 30px;
            li {
                border-bottom: 1px solid #333;
                transform: translateY(10px) translateX(-6px);
                a {
                    font-size: 15px;
                    color: #a2a2a2;
                    display: block;
                    line-height: 45px;
                    text-transform: none;
                    font-weight: 600;
                    &::before {
                        margin-right: 10px;
                        padding-right: 15px;
                        content: "";
                        background: url(../img/btn_page_next.png) no-repeat 50% / cover;
                    }
                    &:hover {
                        color: #d6b161;
                        transition: .6s;
                    }
                }
            }
        }
        .foot_work {
            padding: 0 30px;
        }   
        .work_box{
            transform: translateY(11px) translateX(-5px);
            display: grid;
            grid-template-columns: repeat(3, 67.66px);
            grid-auto-rows: 70px;
            gap: 10px;
            justify-content: center;
            position: relative;
            
            .img_box {
                img {
                    width: 67.66px;
                    display: block;
                }
                a {
                    width: 67.66px; height: 67.66px;
                    font-size: 0px;
                    position: relative;
                    z-index: 9;
                    .txt {
                        width: 67.66px; 
                        height: 67.66px;
                        overflow: hidden;
                        background: rgba(0, 0, 0, .4) url(../img/instagram.svg) no-repeat 50% / 15px;
                        text-indent: -9999px;
                        opacity: 0;
                        transform: translateY(-67.55px);
                        position: relative;
                        z-index: 99;
                    }
                }
                &:hover {
                    .txt {
                        transition: .7s;
                        opacity: 1;
                    }
                }
            }
        }
    }
}

/* footer */
footer .footer_box {
    box-sizing: border-box;
    height: 140px;
    background-color: #1b1b1b;
    color: #6f7273;
    font-size: 15px;
    padding: 35px 0;
    border-top: 1px solid #333;
    border-bottom: 5px solid #222;
    font-weight: 600;
    .box_grid {
        display: grid;
        grid-template-columns: repeat(2, 585px);
        justify-content: center;
        align-items: center;
        .footer_txt {
            padding: 0 30px;
            text-transform: none;
            line-height: 1.5;
            transform: translateY(10px);
            a {
                margin-left: 5px;
                display : inline-block;
                color: #fff;
                &:hover {
                    color: #d6b161;
                    transition: .7s;
                }
            }
            span {
                margin-top: 10px;
            }
        }
        .footer_link {
            padding: 0 30px;
            text-align: right;
            .link_grid {
                display: flex;
                justify-content: right;
                a {
                    display: block;
                    margin-left: 20px;
                    width: 35px;
                    height: 35px;
                    text-align: center;
                    border: 1px solid #5a5a5a;
                    border-radius: 50%;
                    color: #5a5a5a;
                    font-size: 20px;
                    line-height: 35px;
                    img {
                        filter: invert(39%) sepia(2%) saturate(1%) hue-rotate(130deg) brightness(89%) contrast(96%);
                    }
                    &:hover {
                        color: #fff;
                        background-color: #5a5a5a;
                        img {
                            filter: invert(93%) sepia(0%) saturate(7500%) hue-rotate(281deg) brightness(110%) contrast(105%);
                            transition: .7s;
                        }
                        transition: .7s;
                    }
                    .ico1 {
                        width: 20px;
                        height: 20px;
                        transform: translateY(2px);
                    }
                    .ico2 {
                        width: 18px;
                        height: 18px;
                        transform: translateY(2px);
                    }
                    .ico3 {
                        width: 26px;
                        height: 26px;
                        transform: translateY(4px);
                    }
                    .ico4 {
                        width: 24px;
                        height: 24px;
                        transform: translateY(4px);
                    }
                }
            }
        }
    }
}
.footer_Up {
    a {
        width: 40px;
        height: 40px;
        position: fixed;
        right: 20px;
        bottom: 20px;
        background: rgba(0, 0, 0, .6);
        color: #fff;
        img {
            transition: all .3s;
            text-align: center;
            line-height: 40px;
            width: 15px;
            height: 15px;
            transform: translate(85%, 85%) rotate(-90deg);
            filter: brightness(0) invert(1); 
        }
        &:hover {
            background-color: #d6b161;
            color: #7e7e7e;
            transition: .7s;
            img {
                filter: invert(0%) sepia(17%) saturate(515%) hue-rotate(5deg) brightness(50%) contrast(79%);
            }
        }
        
    }
} 
